:global html,
:global body {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  margin: 0;
}
:global #root {
  height: 100%;
  width: 100%;
  position: relative;
}
:global .content {
  width: 90%;
  max-width: 1360px;
  height: 100%;
  margin: 0 auto;
  background-color: #fff;
  box-sizing: border-box;
}
